package org.example.eparish.pages

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.functions.linearGradient
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.rememberPageContext
import com.varabyte.kobweb.silk.components.icons.fa.*
import org.example.eparish.components.Global
import org.example.eparish.components.Routes
import org.example.eparish.components.eparishColors
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.Text

@Composable
fun eParishNavigation() {
    val ctx = rememberPageContext()
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(8.px)
            .gap(14.px)
    ) {

        SidebarItem(
            icon = { FaBorderAll(size = IconSize.LG) },
            label = "Dashboard",
            isSelected = Global.selectedIndex.intValue == 0,
            onClick = {
               Global.selectedIndex.intValue = 0
            }
        )

        SidebarItem(
            icon = { FaUsers(size = IconSize.LG) },
            label = "Catholics",
            isSelected = Global.selectedIndex.intValue == 1,
            onClick = {
                Global.selectedIndex.intValue = 1

            }
        )

        SidebarItem(
            icon = { FaUser(size = IconSize.LG) },
            label = "Leaders",
            isSelected = Global.selectedIndex.intValue == 2,
            onClick = {
                Global.selectedIndex.intValue = 2
            }
        )

        SidebarItem(
            icon = { FaChartBar(size = IconSize.LG) },
            label = "Statistics",
            isSelected = Global.selectedIndex.intValue == 3,
            onClick = {
                Global.selectedIndex.intValue = 3
            }
        )

        SidebarItem(
            icon = { FaRightFromBracket(size = IconSize.LG) },
            label = "Logout",
            isDanger = true,
            isSelected = Global.selectedIndex.intValue == 4,
            onClick = {
                ctx.router.navigateTo(Routes.splash)
            }
        )
    }
}
@Composable
fun SidebarItem(
    icon: @Composable () -> Unit,
    label: String,
    isDanger: Boolean = false,
    isSelected: Boolean = false,
    onClick: () -> Unit
) {
    val backgroundModifier =
        if (Global.selectedIndex.intValue == 4) {
            Modifier.background(Color.transparent)   // always transparent when index = 4
        } else if (isSelected) {
            Modifier.backgroundColor(eparishColors.primary)  // highlighted item
        } else {
            Modifier.backgroundImage(
                linearGradient(
                    eparishColors.secondary,
                    eparishColors.third
                )
            )
        }


    Button(
        attrs = Modifier
            .fillMaxWidth()
            .padding(topBottom = 4.px, leftRight = 4.px)
            .borderRadius(10.px)
            .then(backgroundModifier)
            .cursor(Cursor.Pointer)
            .onClick { onClick() }
            .toAttrs()
    ) {
        Row(
            modifier = Modifier
                .fillMaxWidth()
                .padding(10.px)
                .color(
                        eparishColors.text     // selected text color
                )
                .fontSize(18.px)
                .gap(16.px)
                .fontWeight(FontWeight.Bold),
            horizontalArrangement = Arrangement.Start,
            verticalAlignment = Alignment.CenterVertically
        ) {
            // ICON
            Row(
                modifier = Modifier.size(24.px),
                verticalAlignment = Alignment.CenterVertically
            ) {
                icon()
            }

            // LABEL
            Text(label)
        }
    }
}

