package org.example.eparish

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.flexBasis
import com.varabyte.kobweb.compose.ui.modifiers.flexGrow
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.modifiers.textAlign
import com.varabyte.kobweb.compose.ui.toAttrs
import org.example.eparish.components.eparishColors
import org.example.eparish.components.tableCell
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H5
import org.jetbrains.compose.web.dom.Text

@Composable
fun ResultDataText(text: String, weight: Float, highlight: Boolean = false) {
    val textColor = when {
        text.contains("Unassigned", ignoreCase = true) -> eparishColors.red
        highlight -> eparishColors.primary
        else -> eparishColors.text
    }

    H5(
        attrs = Modifier
            .flexGrow(weight)
            .flexBasis(0.percent)
            .then(tableCell)
            .color(textColor)
            .fontSize(15.px)
            .fontWeight(FontWeight.SemiBold)
            .toAttrs()
    ) {
        Text(text)
    }
}

