package org.example.eparish.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.icons.fa.*
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.example.eparish.components.*
import org.example.eparish.db.Baptism
import org.example.eparish.db.Catholic
import org.example.eparish.db.Marriage
import org.example.eparish.firebase.FirestoreRepository
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*
import kotlin.js.Date

@OptIn(DelicateApi::class)
@Composable
fun Registration() {

    val isLargeScreen = rememberBreakpoint() >= Breakpoint.LG
     var errMessage by remember { mutableStateOf("") }
    var currentStep by remember { mutableIntStateOf(1) }
    var submit by remember { mutableStateOf(false) }
    val totalSteps = 5

    // ------------------------
    // FORM STATE (CLEANED)
    // ------------------------

    var jinaMbatizwa by remember { mutableStateOf("") }
    var tareheKuzaliwa by remember { mutableStateOf("") }
    var tareheKifo by remember { mutableStateOf("") }
    var gender by remember { mutableStateOf("") }
    var hai by remember { mutableStateOf("") }

    var jimbo by remember { mutableStateOf("") }
    var parokia by remember { mutableStateOf("") }
    var parokiaList = remember(jimbo){
        ChurchData.parokiaByJimbo[jimbo]?:emptyList()
    }
    var jumuiya by remember { mutableStateOf("") }
    var nambaSimu by remember { mutableStateOf("") }

    var nambaUbatizoBaba by remember { mutableStateOf("") }
    var nambaUbatizoMama by remember { mutableStateOf("") }

    var jinaMama by remember { mutableStateOf("") }
    var jinaBaba by remember { mutableStateOf("") }

    var nambaUbatizo by remember { mutableStateOf("") }
    var tareheKubatizwa by remember { mutableStateOf("") }
    var jimboKubatizwa by remember { mutableStateOf("") }
    var parokiaKubatizwa by remember { mutableStateOf("") }
    var parokiaKubatizwaList = remember (jimboKubatizwa){
        ChurchData.parokiaByJimbo[jimboKubatizwa]?:emptyList()
    }
    var nambaUbatizoMsimamizi by remember { mutableStateOf("") }
    var jinaMsimamizi by remember { mutableStateOf("") }

    var tareheKomunyo by remember { mutableStateOf("") }
    var tareheKipaimara by remember { mutableStateOf("") }

    var nambaShahada by remember { mutableStateOf("") }
    var tareheNdoa by remember { mutableStateOf("") }

    var jimboNdoa by remember { mutableStateOf("") }
    var parokiaNdoa by remember { mutableStateOf("") }
    var parokiaNdoaList = remember(){
        ChurchData.parokiaByJimbo[jimboNdoa]?:emptyList()
    }
    var nambaMwenzi by remember { mutableStateOf("") }
    var jinaMwenzi by remember { mutableStateOf("") }
    var role by remember {mutableStateOf("")}
    var showError by remember { mutableStateOf(false) }
    LaunchedEffect(
        hai,
        tareheKuzaliwa,
        tareheKifo,
        tareheKubatizwa,
        tareheKomunyo,
        tareheKipaimara,
        tareheNdoa
    ) {
        showError = false
        errMessage = ""

        fun dateOf(str: String): Double? = if (str.isNotEmpty()) Date(str).getTime() else null

        val dBorn = dateOf(tareheKuzaliwa)
        val dDied = dateOf(tareheKifo)
        val dBapt = dateOf(tareheKubatizwa)
        val dKomunyo = dateOf(tareheKomunyo)
        val dKipaimara = dateOf(tareheKipaimara)
        val dNdoa = dateOf(tareheNdoa)

        // ---------------------------
        // BASIC RULE: MUST HAVE BIRTH
        // ---------------------------
        if (dBorn != null) {

            // Sacraments cannot be before birth
            if (dBapt != null && dBapt < dBorn) {
                showError = true
                errMessage = "Ubatizo hauwezi kuwa kabla ya kuzaliwa."
                return@LaunchedEffect
            }
            if (dKomunyo != null && dKomunyo < dBorn) {
                showError = true
                errMessage = "Komunyo haiwezi kuwa kabla ya kuzaliwa."
                return@LaunchedEffect
            }
            if (dKipaimara != null && dKipaimara < dBorn) {
                showError = true
                errMessage = "Kipaimara hakiwezi kuwa kabla ya kuzaliwa."
                return@LaunchedEffect
            }
            if (dNdoa != null && dNdoa < dBorn) {
                showError = true
                errMessage = "Ndoa haiwezi kuwa kabla ya kuzaliwa."
                return@LaunchedEffect
            }
        }

        // ------------------------------------
        // SACRAMENT ORDER ENFORCEMENT
        // ------------------------------------
        // They can be equal (same day), but not backwards.

        // Komunyo  Ubatizo
        if (dKomunyo != null && dBapt != null && dKomunyo < dBapt) {
            showError = true
            errMessage = "Komunyo haiwezi kuwa kabla ya ubatizo."
            return@LaunchedEffect
        }

        // Kipaimara  Komunyo
        if (dKipaimara != null && dKomunyo != null && dKipaimara < dKomunyo) {
            showError = true
            errMessage = "Kipaimara hakiwezi kuwa kabla ya komunyo."
            return@LaunchedEffect
        }

        // Kipaimara  Ubatizo
        if (dKipaimara != null && dBapt != null && dKipaimara < dBapt) {
            showError = true
            errMessage = "Kipaimara hakiwezi kuwa kabla ya ubatizo."
            return@LaunchedEffect
        }

        // Ndoa  Kipaimara (if kipaimara exists)
        if (dNdoa != null && dKipaimara != null && dNdoa < dKipaimara) {
            showError = true
            errMessage = "Ndoa haiwezi kuwa kabla ya kipaimara."
            return@LaunchedEffect
        }

        // Ndoa  Komunyo (in case no kipaimara)
        if (dNdoa != null && dKomunyo != null && dNdoa < dKomunyo) {
            showError = true
            errMessage = "Ndoa haiwezi kuwa kabla ya komunyo."
            return@LaunchedEffect
        }

        // Ndoa  Ubatizo
        if (dNdoa != null && dBapt != null && dNdoa < dBapt) {
            showError = true
            errMessage = "Ndoa haiwezi kuwa kabla ya ubatizo."
            return@LaunchedEffect
        }

        // ------------------------------------
        // RULES WHEN DECEASED
        // ------------------------------------
        if (hai == "Hapana" && dDied != null) {

            if (dBapt != null && dBapt > dDied) {
                showError = true
                errMessage = "Ubatizo hauwezi kuwa baada ya kifo."
                return@LaunchedEffect
            }

            if (dKomunyo != null && dKomunyo > dDied) {
                showError = true
                errMessage = "Komunyo haiwezi kuwa baada ya kifo."
                return@LaunchedEffect
            }

            if (dKipaimara != null && dKipaimara > dDied) {
                showError = true
                errMessage = "Kipaimara hakiwezi kuwa baada ya kifo."
                return@LaunchedEffect
            }

            if (dNdoa != null && dNdoa > dDied) {
                showError = true
                errMessage = "Ndoa haiwezi kuwa baada ya kifo."
                return@LaunchedEffect
            }
        }
    }


    val legendTitle = when (currentStep) {
        1 -> "Taarifa Binafsi"
        3 -> "Taarifa za Wazazi"
        2->  "Taarifa Za Ubatizo"
        4-> "Komunyo ya Kwanza na Kipaimara"
        5-> "Ndoa Takatifu"
        else -> ""
    }

    // ------------------------
    // VALIDATION FUNCTION
    // ------------------------
    fun String.isNumeric(): Boolean {
        return this.isNotEmpty() && this.all { it.isDigit() }
    }

    fun isStepValid(step: Int): Boolean {
        return when (step) {

            1 -> when (hai) {
                "Ndiyo" -> {
                    jinaMbatizwa.isNotEmpty() &&
                            tareheKuzaliwa.isNotEmpty() &&
                            gender.isNotEmpty() &&
                            jimbo.isNotEmpty() &&
                            parokia.isNotEmpty() &&
                            jumuiya.isNotEmpty()
                }
                "Hapana" -> {
                    jinaMbatizwa.isNotEmpty() &&
                            tareheKuzaliwa.isNotEmpty() &&
                            gender.isNotEmpty() &&
                            tareheKifo.isNotEmpty()
                }
                else -> false
            }

            2 -> {
                nambaUbatizo.isNumeric() &&
                        tareheKubatizwa.isNotEmpty()&&
                        jimboKubatizwa.isNotEmpty() &&
                        parokiaKubatizwa.isNotEmpty()&&
                        jinaMsimamizi.isNotEmpty() &&
                        nambaUbatizoMsimamizi.isNumeric()

            }
            3 -> {
                nambaUbatizoBaba.isNumeric() &&
                        nambaUbatizoMama.isNumeric() &&
                        jinaBaba.isNotEmpty() &&
                        jinaMama.isNotEmpty() && nambaUbatizoMama.trim() != nambaUbatizoBaba.trim() && jinaMama.trim() != jinaBaba.trim()
            }

            else -> true
        }
    }


    val enabledCondition = isStepValid(currentStep)

    // ------------------------
    // UI START
    // ------------------------

    Column(modifier = Modifier.fillMaxSize()) {

        // =======================
        // HEADER
        // =======================

        Row(
            modifier = Modifier.fillMaxWidth()
                .fontSize(22.px)
                .fontWeight(FontWeight.Bold)
                .color(eparishColors.primary),
            horizontalArrangement = Arrangement.Start,
            verticalAlignment = Alignment.CenterVertically
        ) {
            Span(attrs = Modifier.weight(1f).toAttrs())
            Text("Hatua ya $currentStep/$totalSteps")
        }

        // =======================
        // FORM BODY
        // =======================

        Row(
            modifier = Modifier.fillMaxSize().margin(topBottom = 16.px),
            horizontalArrangement = if (isLargeScreen) Arrangement.spacedBy(16.px) else Arrangement.Center
        ) {
            Form(attrs = Modifier.fillMaxSize().toAttrs()) {

                Fieldset {

                    Legend(
                        attrs = Modifier.fontSize(20.px)
                            .fontWeight(FontWeight.Bold)
                            .color(eparishColors.text)
                            .toAttrs()
                    ) {
                        Text(legendTitle)
                    }

                    // ============================
                    // STEP 1  TAARIFA BINAFSI
                    // ============================

                    if (currentStep == 1) {

                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel("jinaMbatizwa", "Jina Kamili") { FaCircleUser() }
                                CustomInputText(
                                    value = jinaMbatizwa,
                                    onValueChange = { input ->
                                        if (input.length <= 50)
                                            jinaMbatizwa = input.uppercase()
                                    }
                                )
                            },
                            col2 = {
                                CustomLabel("tareheKuzaliwa", "Tarehe ya Kuzaliwa") { FaCalendar() }
                                CustomInputDate(
                                    value = tareheKuzaliwa,
                                    onValueChange = { tareheKuzaliwa = it }
                                )
                            }
                        )

                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel("jinsia", "Jinsia") { FaMarsAndVenus() }
                                CustomSelect(
                                    value = gender,
                                    onValueChange = { gender = it },
                                    selectList = genderList
                                )
                            },
                            col2 = {
                                CustomLabel("hai", "Yupo Hai?") { FaCircleInfo() }
                                CustomSelect(
                                    value = hai,
                                    onValueChange = { hai = it },
                                    selectList = aliveList
                                )
                            }
                        )

                        // If Alive  Jimbo/Parokia/Jumuiya
                        if (hai == "Ndiyo") {

                            ResponsiveFormRow(
                                col1 = {
                                    CustomLabel("jimbo", "Jimbo Una/Analoishi") { FaChurch() }
                                    CustomSelect(
                                        value = jimbo,
                                        onValueChange = { jimbo = it },
                                        selectList = majimbo
                                    )
                                },
                                col2 = {
                                    CustomLabel("parokia", "Parokia Una/Anayosali") { FaPlaceOfWorship() }
                                    CustomSelect(
                                        value = parokia,
                                        onValueChange = { parokia = it },
                                        selectList = parokiaList
                                    )
                                }
                            )

                            ResponsiveFormRow(
                                col1 = {
                                    CustomLabel("jumuiya", "Jumuiya Una/Anayosali") { FaPeopleGroup() }
                                    CustomSelect(
                                        value = jumuiya,
                                        onValueChange = { jumuiya = it },
                                        selectList = aliveList
                                    )
                                },
                                col2 = {
                                    CustomLabel("simu", "Namba ya simu") { FaPhoneFlip() }
                                    CustomInputPhone(
                                        value = nambaSimu,
                                        onValueChange = {
                                            if(it.length <=10){
                                                nambaSimu = it

                                            }
                                        }
                                    )
                                }
                            )

                        } else if (hai == "Hapana") {

                            ResponsiveFormRow(
                                col1 = {
                                    CustomLabel("kifo", "Tarehe ya Kifo") { FaCalendarDays() }
                                    CustomInputDate(
                                        value = tareheKifo,
                                        onValueChange = { tareheKifo = it }
                                    )
                                },
                                col2 = {}
                            )
                        }
                    }

                    // ============================
                    // STEP 3  TAARIFA ZA WAZAZI
                    // ============================

                    if (currentStep == 3) {

                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel(
                                    id = "nambaUbatizoBaba",
                                    label = "Namba ya Ubatizo (Baba)",
                                ) { FaIdBadge(style = IconStyle.FILLED) }
                                CustomInputText(
                                    value = nambaUbatizoBaba,
                                    onValueChange = {
                                        if (it.length <= 10 && it.matches(Regex("^[0-9]*\$"))) nambaUbatizoBaba = it
                                    }
                                )


                            },
                            col2 = {
                                CustomLabel("jinaBaba", "Jina Kamili (Baba)") { FaCircleUser() }
                                CustomInputText(
                                    value = jinaBaba,
                                    onValueChange = { input ->
                                        if (input.length <= 50)
                                            jinaBaba = input.uppercase()
                                    }
                                )
                            }
                        )
                        ResponsiveFormRow(col1 = {
                            CustomLabel(
                                id = "nambaUbatizoMama",
                                label = "Namba ya Ubatizo (Mama)",
                            ) { FaIdBadge(style = IconStyle.FILLED) }
                            CustomInputText(
                                value = nambaUbatizoMama,
                                onValueChange = {
                                    if (it.length <= 10 && it.matches(Regex("^[0-9]*\$"))) {
                                        nambaUbatizoMama = it
                                    }
                                }
                            )
                        }, col2 = {
                            CustomLabel("jinaMama", "Jina Kamili (Mama)") { FaCircleUser() }
                            CustomInputText(
                                value = jinaMama,
                                onValueChange = { input ->
                                    if (input.length <= 50)
                                        jinaMama = input.uppercase()
                                }
                            )
                        })
                    }
                    // ============================
                    // STEP 2  TAARIFA ZA UBATIZO
                    // ============================
                    if (currentStep == 2 ){
                       ResponsiveFormRow(col1 = {
                           CustomLabel(
                               id = "nambaUbatizo",
                               label = "Namba ya Ubatizo",
                           ) { FaIdBadge(style = IconStyle.FILLED) }
                           CustomInputText(
                               value = nambaUbatizo,
                               onValueChange = {
                                   if (it.length <= 10 && it.matches(Regex("^[0-9]*\$"))) {
                                       nambaUbatizo = it
                                   }
                               }
                           )
                       }, col2 = {
                           CustomLabel("tareheKubatizwa", "Tarehe ya Kubatizwa") { FaCalendarDays() }
                           CustomInputDate(
                               value = tareheKubatizwa,
                               onValueChange = { tareheKubatizwa = it }
                           )
                       })
                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel("jimboKubatizwa", "Jimbo Ulilobatiziwa") { FaChurch() }
                                CustomSelect(
                                    value = jimboKubatizwa,
                                    onValueChange = { jimboKubatizwa = it },
                                    selectList = majimbo
                                )
                            },
                            col2 = {
                                CustomLabel("parokiaKubatizwa", "Parokia Uliyobatiziwa") { FaPlaceOfWorship() }
                                CustomSelect(
                                    value = parokiaKubatizwa,
                                    onValueChange = { parokiaKubatizwa = it },
                                    selectList = parokiaKubatizwaList
                                )
                            }
                        )
                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel(
                                    id = "nambaUbatizoMsimamizi",
                                    label = "Namba ya Ubatizo (Msimamizi)",
                                ) { FaIdBadge(style = IconStyle.FILLED) }
                                CustomInputText(
                                    value = nambaUbatizoMsimamizi,
                                    onValueChange = {
                                        if (it.length <= 10 && it.matches(Regex("^[0-9]*\$"))) nambaUbatizoMsimamizi = it
                                    }
                                )


                            },
                            col2 = {
                                CustomLabel("jinaMsimamizi", "Jina Kamili (Msimamizi)") { FaCircleUser() }
                                CustomInputText(
                                    value = jinaMsimamizi,
                                    onValueChange = { input ->
                                        if (input.length <= 50)
                                            jinaMsimamizi = input.uppercase()
                                    }
                                )
                            }
                        )
                    }

                    // ============================
                    //TAARIFA ZA KOMUNYO NA KIPAIMARA
                    // ============================
                    if(currentStep == 4){
                        ResponsiveFormRow(col1 = {
                            CustomLabel("tareheKomunyo", "Tarehe ya Komunyo") { FaCalendar() }
                            CustomInputDate(
                                value = tareheKomunyo,
                                onValueChange = { tareheKomunyo = it }
                            )
                        }, col2 = {
                            CustomLabel("tareheKipaimara", "Tarehe ya Kipaimara") { FaCalendar() }
                            CustomInputDate(
                                value = tareheKipaimara,
                                onValueChange = { tareheKipaimara = it }
                            )
                        })
                    }
                    // ============================
                    // STEP 5  TAARIFA ZA NDOA
                    if (currentStep == 5){
                        ResponsiveFormRow(col1 = {
                            CustomLabel(
                                id = "nambaShahada",
                                label = "Namba ya Shahada",
                            ) { FaRing() }
                            CustomInputText(
                                value = nambaShahada,
                                onValueChange = {
                                    if (it.length <= 10 && it.matches(Regex("^[0-9]*\$"))) nambaShahada = it
                                }
                            )
                        }, col2 = {
                            CustomLabel("tareheNdoa", "Tarehe ya Ndoa") { FaCalendar() }
                            CustomInputDate(
                                value = tareheNdoa,
                                onValueChange = { tareheNdoa = it }
                            )
                        })
                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel("jimboNdoa", "Jimbo Ndoa ilipofungwa") { FaChurch() }
                                CustomSelect(
                                    value = jimboNdoa,
                                    onValueChange = { jimboNdoa = it },
                                    selectList = majimbo
                                )
                            },
                            col2 = {
                                CustomLabel("parokiaNdoa", "Parokia Ndoa ilipofungwa") { FaPlaceOfWorship() }
                                CustomSelect(
                                    value = parokiaNdoa,
                                    onValueChange = { parokiaNdoa = it },
                                    selectList = parokiaNdoaList
                                )
                            }
                        )
                        ResponsiveFormRow(
                            col1 = {
                                CustomLabel(
                                    id = "nambaMwenzi",
                                    label = "Namba ya Ubatizo (Mwenzi)",
                                ) { FaIdBadge(style = IconStyle.FILLED) }
                                CustomInputText(
                                    value = nambaMwenzi,
                                    onValueChange = {
                                        if (it.length <= 10 && it.matches(Regex("^[0-9]*\$"))) nambaMwenzi = it
                                    }
                                )


                            },
                            col2 = {
                                CustomLabel("jinaMwenzi", "Jina Kamili (Mwenzi)") { FaCircleUser() }
                                CustomInputText(
                                    value = jinaMwenzi,
                                    onValueChange = { input ->
                                        if (input.length <= 50)
                                            jinaMwenzi = input.uppercase()
                                    }
                                )
                            }
                        )
                    }
                    // ============================
                    // NAVIGATION BUTTONS
                    // ============================
                    ResponsiveFormRow(col1 = {}, col2 = {

                        Row(
                            modifier = Modifier.fillMaxWidth().gap(8.px),
                            horizontalArrangement = Arrangement.End,
                            verticalAlignment = Alignment.CenterVertically
                        ) {

                            if (currentStep > 1 && !submit) {
                                Button(
                                    modifier = sitishaButtonStyle.toModifier(),
                                    onClick = { currentStep-- }
                                ) {
                                    Row(horizontalArrangement = Arrangement.spacedBy(6.px), verticalAlignment = Alignment.CenterVertically) {
                                        FaArrowLeft(size = IconSize.XL, modifier = Modifier.color(eparishColors.text))
                                        if (isLargeScreen) Text("Rudi")
                                    }
                                }
                            }
                             if(!submit){
                                 Button(
                                     enabled = enabledCondition && !showError,
                                     modifier = endeleaButtonStyle.toModifier(),
                                     onClick = {
                                         if (currentStep < totalSteps) currentStep++
                                         if(currentStep == 5) submit = true
                                     }
                                 ) {
                                     Row(horizontalArrangement = Arrangement.spacedBy(6.px), verticalAlignment = Alignment.CenterVertically) {
                                         FaArrowRight(size = IconSize.XL, modifier = Modifier.color(eparishColors.text))
                                         if (isLargeScreen) Text("Endelea")
                                     }
                                 }
                             }

                            // A mutable list to hold registered Catholics
                            val registeredCatholics = remember { mutableStateListOf<Catholic>() }

                            if(currentStep == 5 && submit){
                                Button(
                                    modifier = endeleaButtonStyle.toModifier(),
                                    onClick = {
                                        // Construct Baptism object
                                        val baptismInfo = Baptism(
                                            nambaUbatizo = nambaUbatizo,
                                            tareheKubatizwa = tareheKubatizwa,
                                            jimboKubatizwa = jimboKubatizwa,
                                            parokiaKubatizwa = parokiaKubatizwa,
                                            nambaUbatizoMsimamizi = nambaUbatizoMsimamizi,
                                            jinaMsimamizi = jinaMsimamizi
                                        )

                                        // Construct Marriage object if info exists
                                        val marriageInfo = if (nambaShahada.isNotBlank() && tareheNdoa.isNotBlank()) {
                                            Marriage(
                                                nambaShahada = nambaShahada,
                                                tareheNdoa = tareheNdoa,
                                                jimboNdoa = jimboNdoa,
                                                parokiaNdoa = parokiaNdoa,
                                                nambaMwenzi = nambaMwenzi,
                                                jinaMwenzi = jinaMwenzi
                                            )
                                        } else null

                                        // Construct Catholic object
                                        val newCatholic = Catholic(
                                            jinaMbatizwa = jinaMbatizwa,
                                            tareheKuzaliwa = tareheKuzaliwa,
                                            gender = gender,
                                            hai = hai,
                                            jimbo = jimbo,
                                            parokia = parokia,
                                            jumuiya = jumuiya,
                                            nambaSimu = nambaSimu,
                                            parentBaptismNumbers = Pair(nambaUbatizoBaba, nambaUbatizoMama),
                                            jinaMama = jinaMama,
                                            jinaBaba = jinaBaba,
                                            baptism = baptismInfo,
                                            communionDate = tareheKomunyo,
                                            confirmationDate = tareheKipaimara,
                                            marriage = marriageInfo,
                                            role = role
                                        )

                                        // Save to Firestore (returns a JS Promise)
                                        FirestoreRepository.saveCatholic(newCatholic)
                                            .then {
                                                // success - optional UI feedback
                                                println("Registration saved to Firestore")
                                                // reset / navigate or set flags as desired
                                                submit = false
                                                // Optionally clear form or move to first step:
                                                currentStep = 1
                                            }



                                    }


                                ) {
                                    Row(horizontalArrangement = Arrangement.spacedBy(6.px), verticalAlignment = Alignment.CenterVertically) {
                                        FaPaperPlane(size = IconSize.XL, modifier = Modifier.color(eparishColors.text))
                                        if (isLargeScreen) Text("Kamilisha usajili")
                                    }
                                }
                            }

                        }
                    })
                    if(showError){
                        ResponsiveFormRow(col1 = {
                            H5(
                                attrs = Modifier.color(eparishColors.red)
                                    .toAttrs()
                            ) {
                                Text(errMessage)
                            }
                        }, col2 = {

                        })
                    }

                }
            }
        }
    }
}
