package org.example.eparish.db

data class Catholic(
    val jinaMbatizwa: String,
    val tareheKuzaliwa: String,
    val gender: String,
    val hai: String,
    val jimbo: String,
    var parokia: String,
    val jumuiya: String,
    val nambaSimu: String,
    val parentBaptismNumbers: Pair<String, String>,
    val jinaMama: String,
    val jinaBaba: String,
    val baptism: Baptism,
    var communionDate: String,
    var confirmationDate: String,
    var marriage: Marriage?,
    var role: String
)
data class Baptism(
    val nambaUbatizo: String,
    val tareheKubatizwa: String,
    val jimboKubatizwa: String,
    val parokiaKubatizwa: String,
    var nambaUbatizoMsimamizi: String,
    var jinaMsimamizi: String
)

data class Marriage(
    var nambaShahada: String,
    var tareheNdoa: String,
    var jimboNdoa: String,
    var parokiaNdoa: String,
    var nambaMwenzi: String,
    var jinaMwenzi: String
)
