@Composable
fun CustomInputText(
    value: String,
    onValueChange: (String) -> Unit // pass new value to the caller
) {
    Input(
        modifier = Modifier.fillMaxWidth(),
        value = value,
        onValueChange = { newValue ->
            onValueChange(newValue)
        },
        type = InputType.Text
    )
}
@Composable
fun CustomInputNumber(
    value: Number,
    onValueChange: (String) -> Unit // pass new value to the caller
) {
    Input(
        modifier = Modifier.fillMaxWidth(),
        value = value,
        onValueChange = { newValue ->
            onValueChange(newValue.toString())
        },
        type = InputType.Number
    )
}
