@Composable
fun CustomSelect(
    value: String,
    onValueChange: (String) -> Unit,
    selectList: List<String>
) {
    Select(
        attrs = Modifier
            .fillMaxWidth().padding(8.px)
            .toAttrs {
                onChange { event ->
                    val selected: String = event.value.toString()
                    onValueChange(selected)
                }
            }
    ) {
        // Default placeholder
        Option(
            value = "",
            attrs = {
                if (value.isEmpty()) attr("selected", "true")
                attr("disabled", "true")
            }
        ) {
            Text("Chagua...")
        }

        // Options
        selectList.forEach { item ->
            Option(
                value = item,
                attrs = {
                    if (value == item) attr("selected", "true")
                }
            ) {
                Text(item)
            }
        }
    }
}
val tableCell = Modifier
    .display(DisplayStyle.Flex)
    .flexDirection(FlexDirection.Row)
    .alignItems(AlignItems.Center)
    .justifyContent(JustifyContent.Center)
    .padding(6.px)