package org.example.eparish.pages

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.ObjectFit
import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import org.example.eparish.components.eparishColors
import org.jetbrains.compose.web.css.DisplayStyle
import org.jetbrains.compose.web.css.FlexWrap
import org.jetbrains.compose.web.css.JustifyContent
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Text

@Composable
fun Dashboard() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(16.px)
            .color(eparishColors.primary)
            .gap(16.px)
    ) {
        Row(
            modifier = Modifier.fillMaxWidth()
                .padding(leftRight = 16.px)
                .color(eparishColors.primary)
                .fontSize(18.px)
                .fontWeight(FontWeight.Bold)
        ) {
            Text("Habari! lennycodes Karibu e-Parish")
        }
        ResponsiveRow {
            DashboardItem("three.jpeg")
            DashboardItem("image.jpg")
            DashboardItem("four.jpg")
        }
    }
}

/* ---------------------------
   Responsive Row Component
---------------------------- */
@Composable
fun ResponsiveRow(content: @Composable RowScope.() -> Unit) {
    Row(
        modifier = Modifier
            .fillMaxWidth()
            .display(DisplayStyle.Flex)
            .flexWrap(FlexWrap.Wrap)
            .justifyContent(JustifyContent.Center)
            .gap(16.px),
        content = content
    )
}

/* ---------------------------
   Dashboard Card
---------------------------- */
@Composable
fun DashboardItem(src: String) {
    Box(
        modifier = Modifier
            .width(300.px)
            .height(300.px)
            .borderRadius(12.px)
            .overflow(Overflow.Hidden)
            .backgroundColor(org.jetbrains.compose.web.css.rgb(245, 245, 245))
         ,
                contentAlignment = Alignment.Center
    ) {
        Image(
            src = src,
            modifier = Modifier
                .fillMaxSize()
                .objectFit(ObjectFit.Cover)
        )
    }
}
