package org.example.eparish.firebase

import kotlinx.coroutines.await
import org.example.eparish.db.Baptism
import kotlin.js.Promise
import org.example.eparish.db.Catholic
import org.example.eparish.db.Marriage

object FirestoreRepository {

    // Firestore instance
    private val db = getFirestore(firebaseApp)

    fun saveCatholic(catholic: Catholic): Promise<Unit> {
        val colRef = collection(db, "catholics")
        val docRef = if (catholic.baptism.nambaUbatizo.isNotEmpty()) {
            doc(colRef, catholic.baptism.nambaUbatizo) // Use baptism number as document ID
        } else {
            doc(colRef) // Auto-generated ID
        }

        val data = js("{}")
        // ---------------------------
        // Personal info
        // ---------------------------
        data.jinaMbatizwa = catholic.jinaMbatizwa
        data.tareheKuzaliwa = catholic.tareheKuzaliwa
        data.gender = catholic.gender
        data.hai = catholic.hai
        data.jimbo = catholic.jimbo
        data.parokia = catholic.parokia
        data.jumuiya = catholic.jumuiya
        data.nambaSimu = catholic.nambaSimu

        // ---------------------------
        // Parents
        // ---------------------------
        data.nambaUbatizoBaba = catholic.parentBaptismNumbers.first
        data.nambaUbatizoMama = catholic.parentBaptismNumbers.second
        data.jinaMama = catholic.jinaMama
        data.jinaBaba = catholic.jinaBaba

        // ---------------------------
        // Baptism
        // ---------------------------
        data.baptism = js("{}")
        data.baptism.nambaUbatizo = catholic.baptism.nambaUbatizo
        data.baptism.tareheKubatizwa = catholic.baptism.tareheKubatizwa
        data.baptism.jimboKubatizwa = catholic.baptism.jimboKubatizwa
        data.baptism.parokiaKubatizwa = catholic.baptism.parokiaKubatizwa
        data.baptism.nambaUbatizoMsimamizi = catholic.baptism.nambaUbatizoMsimamizi
        data.baptism.jinaMsimamizi = catholic.baptism.jinaMsimamizi

        // ---------------------------
        // Sacraments
        // ---------------------------
        data.tareheKomunyo = catholic.communionDate
        data.tareheKipaimara = catholic.confirmationDate

        // ---------------------------
        // Marriage
        // ---------------------------
        catholic.marriage?.let { marriage ->
            data.marriage = js("{}")
            data.marriage.nambaShahada = marriage.nambaShahada
            data.marriage.tareheNdoa = marriage.tareheNdoa
            data.marriage.jimboNdoa = marriage.jimboNdoa
            data.marriage.parokiaNdoa = marriage.parokiaNdoa
            data.marriage.nambaMwenzi = marriage.nambaMwenzi
            data.marriage.jinaMwenzi = marriage.jinaMwenzi
        }

        data.role = catholic.role

        return setDoc(docRef, data)
    }
    //retrieve from catholics
    suspend fun getAllCatholics(): List<Catholic> {
        val colRef = collection(db, "catholics")
        val snapshot = getDocs(colRef).await()
        val list = mutableListOf<Catholic>()

        // Access docs array from snapshot
        val docsArray = js("snapshot.docs") as Array<dynamic>

        for (doc in docsArray) {
            val data = js("doc.data()")

            // Baptism
            val baptismData = data.baptism
            val baptism = Baptism(
                nambaUbatizo = baptismData?.nambaUbatizo ?: "",
                tareheKubatizwa = baptismData?.tareheKubatizwa ?: "",
                jimboKubatizwa = baptismData?.jimboKubatizwa ?: "",
                parokiaKubatizwa = baptismData?.parokiaKubatizwa ?: "",
                nambaUbatizoMsimamizi = baptismData?.nambaUbatizoMsimamizi ?: "",
                jinaMsimamizi = baptismData?.jinaMsimamizi ?: ""
            )

            // Marriage
            val marriageData = data.marriage
            val marriage = if (marriageData != null) {
                Marriage(
                    nambaShahada = marriageData.nambaShahada ?: "",
                    tareheNdoa = marriageData.tareheNdoa ?: "",
                    jimboNdoa = marriageData.jimboNdoa ?: "",
                    parokiaNdoa = marriageData.parokiaNdoa ?: "",
                    nambaMwenzi = marriageData.nambaMwenzi ?: "",
                    jinaMwenzi = marriageData.jinaMwenzi ?: ""
                )
            } else null

            // Parents
            val parentPair = Pair(
                data.nambaUbatizoBaba ?: "",
                data.nambaUbatizoMama ?: ""
            )

            // Construct Catholic object
            val catholic = Catholic(
                jinaMbatizwa = data.jinaMbatizwa ?: "",
                tareheKuzaliwa = data.tareheKuzaliwa ?: "",
                gender = data.gender ?: "",
                hai = data.hai ?: "",
                jimbo = data.jimbo ?: "",
                parokia = data.parokia ?: "",
                jumuiya = data.jumuiya ?: "",
                nambaSimu = data.nambaSimu ?: "",
                parentBaptismNumbers = parentPair,
                jinaMama = data.jinaMama ?: "",
                jinaBaba = data.jinaBaba ?: "",
                baptism = baptism,
                communionDate = data.tareheKomunyo ?: "",
                confirmationDate = data.tareheKipaimara ?: "",
                marriage = marriage,
                role = data.role ?: ""
            )

            list.add(catholic)
        }

        return list
    }
}