package org.example.eparish.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.functions.linearGradient
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.core.rememberPageContext
import kotlinx.coroutines.delay
import org.example.eparish.components.Routes
import org.example.eparish.components.eparishColors
import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.vh
import org.jetbrains.compose.web.css.vw
import org.jetbrains.compose.web.dom.H5
import org.jetbrains.compose.web.dom.Img
import org.jetbrains.compose.web.dom.Text

@Page("/splash")
@Composable
fun Splash() {
    val ctx = rememberPageContext()

    LaunchedEffect(Unit) {
        delay(3500)
        ctx.router.navigateTo(Routes.login)
    }

    Box(
        modifier = Modifier
            .width(100.vw)
            .height(100.vh)
            .backgroundImage(linearGradient(eparishColors.secondary, eparishColors.third)),
        contentAlignment = Alignment.Center
    ) {
        Column(
            verticalArrangement = Arrangement.spacedBy(8.px),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Img(
                src = "logo.png",
                alt = "logo",
                attrs = Modifier
                    .borderRadius(50.percent)
                    .size(200.px)
                    .toAttrs()
            )
        }

        H5(
            attrs = Modifier
                .position(Position.Absolute)
                .bottom(16.px)
                .color(eparishColors.primary)
                .fontWeight(FontWeight.Bold)
                .toAttrs()
        ) {
            Text("Developed by lennycodes")
        }
    }
}

